@use '../../../../variables' as variables;

@mixin flex-column-center($gap: 0) {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: $gap;
}
:host {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 500px;
  // align-items: center;
}

.pt-icon {
  transform: scale(1.4);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 680px) {
    transform: scale(1);
  }
}

.card-border-gradient {
  padding: 1px;
  border-radius: 12px;
  background-image: var(--cardBorderGradient);
  max-width: 500px;
  min-width: 450px;
  box-shadow: var(--boxShadow);

  @media (max-width: 420px) {
    border-radius: 8px;
  }
}
.received-payment-outer-container {
  @include flex-column-center($gap: variables.$global-light-spacing-4);
  align-items: center;
  width: 100%;

  @media (min-width: 1200px) {
    align-items: flex-start;
  }

  @media (max-width: 420px) {
    padding: variables.$global-light-spacing-2;
    gap: variables.$global-light-spacing-2;
  }
}

.received-payment-card {
  @include flex-column-center($gap: variables.$global-light-spacing-2);
  // max-width: 480px;
  // min-width: 500px;
  width: 100%;
  background-image: var(
    --cardBackground,
    radial-gradient(at top left, #ffffff 0%, #ffffff 75%),
  );
  border-radius: 12px;
  padding: variables.$global-light-spacing-6 variables.$global-light-spacing-4;
  //   filter: drop-shadow(0px 0px 8px variables.$global-light-gray-gray-4);

  @media (max-width: 420px) {
    border-radius: 8px;
    padding: variables.$global-light-spacing-5 variables.$global-light-spacing-3;
    gap: variables.$global-light-spacing-4;
  }
}

.rpc-top {
  @include flex-column-center($gap: variables.$global-light-spacing-3);
  > h5 {
    color: var(--neutralColor1, variables.$global-light-gray-gray-10);
    @media (max-width: 420px) {
      font-size: variables.$global-light-font-size-3;
    }
  }
}
.body3.color-gray-7 {
  color: var(--neutralColor5);
}
.body1.color-gray-7 {
  color: var(--neutralColor5);
}
.body1.bold.color-gray-10 {
  color: var(--neutralColor1, variables.$global-light-gray-gray-10);
}
.receipt-value.accentuate-text {
  color: var(--primaryColor);
}
.receipt-value.color-gray-9 {
  color: var(--neutralColor1, variables.$global-light-gray-gray-9);
}
.rpc-bottom {
  display: flex;
  flex-direction: column;
  gap: variables.$global-light-spacing-1;
  padding-top: variables.$global-light-spacing-1;
  min-width: 220px;
}

.rpc-img-outer-container {
  width: 180px;
  height: 180px;
  border-radius: 1000px;
  background-color: var(--secondaryColor2, rgba(255, 255, 255, 0));
  display: flex;
  align-items: center;
  justify-content: center;
}

.rpc-img-container {
  width: 110px;
  @media (max-width: 420px) {
    width: 100px;
  }
}

.rpc-mid {
  @include flex-column-center($gap: variables.$global-light-spacing-1);
  p {
    text-align: center;
  }
  span {
    white-space: nowrap;
  }
}

.accentuate-text {
  // color: variables.$global-light-daybreak-blue-blue-6;
  color: var(--primaryColor);
}

@media (max-width: 600px) {
  .received-payment-card {
    min-width: 0;
  }
}

@media print {
  // :host,
  // .received-payment-outer-container {
  //   align-items: center;
  //   justify-content: center;
  // }
  .received-payment-card {
    display: block;
    align-self: center;
  }
  .received-payment-card {
    button {
      display: none;
    }
  }
  .rpc-top {
    > h5 {
      color: variables.$global-light-gray-gray-10;
    }
  }

  .body3.color-gray-7 {
    color: variables.$global-light-gray-gray-7;
  }
  .body1.color-gray-7 {
    color: variables.$global-light-gray-gray-7;
  }
  .body1.bold.color-gray-10 {
    color: variables.$global-light-gray-gray-10;
  }
}

@media (max-width: 600px) {
  .card-border-gradient {
    min-width: 0px;
  }
}
